import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import {Link } from "react-router-dom"; 
import Loader from 'react-loader-spinner';
import { FiChevronsRight } from "react-icons/fi";
import Moment from 'react-moment';
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import PropertyimgEdittwo from "./PropertyimgEdittwo";
import '../../styles/_property.scss';


const PropertyimgEdit = (props) => {
  const {viewallsssall} = useContext(MyContext);
  useEffect(() => {
    viewallsss(props.match.params.AddPreview);
    window.scrollTo(0, 0);
  }, []);

  const [isviewallsss, setviewallsss] = useState([]);
  const viewallsss = async(ids) =>{
    try {
      const viewallsssqqq = await viewallsssall(ids);
      if(viewallsssqqq.success === 1){
        console.log('viewallsssqqq',viewallsssqqq.viewalllist);
        setviewallsss(viewallsssqqq.viewalllist);
        return;
      }
    } catch (error) { throw error;}
  }
  return (
    <>
    <Navbarmenu />
  <div className="Rentsssviews"> 
    <div className="container"> 
      <div className="rows"> 
      {isviewallsss.length > 0 ? <> 
        <PropertyimgEdittwo listsss={isviewallsss} />
      </> : null }
      </div>
</div>
</div>

    <Footer />         
			  </>
  )
}

export default PropertyimgEdit
